import React, { useEffect, useState } from "react";
import "../Description/Description.scss";
import gsap from "gsap";
import TextPlugin from "gsap/TextPlugin";

function Description() {
  gsap.registerPlugin(TextPlugin);
  useEffect(() => {
   

    gsap.to("#quote", {
      duration: 15,
      text: {
        value:
          "L'intelligence artificielle est un processus d'imitation de lintelligence humaine qui repose sur la cration et l'application d'algorithmes excuts dans un environnement informatique dynamique. Son but est de permettre  des ordinateurs de penser et d'agir comme des tres humains.",
        delimiter: " ",
      },

      easy: "none",
    });
  }, []);

  return (
    <div className="desc-homepage">
      <h1 >L'intelligence artificielle (IA) </h1>
      <div id="quote"></div>
    </div>
  );
}

export default Description;
